:root {
  --textbg: #ff7ac6;
  --bgcolor: #414558;
  --stextbg: #fda4af;
  --fbg: #272935;
  --second: #bf95f9;
  --desc: #cbd5e1;
}

* {
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  margin: 0;
  padding: 0;
}

.home {
  background: url("./asset/images/home2.jpg");
  object-fit: cover;
  background-size: cover;
  background-position: center;
  height: 100vh;
  width: 100%;
}

.home .tagline {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90%;
  color: #fffffe;
  font-size: 1.5rem;
  text-shadow: 2px 2px 8px var(--bgcolor);
  flex-direction: column;
}
.home .tagline h2 {
  margin-bottom: 1rem;
}

.home .tagline button {
  background-color: #ff7ac6;
  color: #272935;
  border: none;
  padding: 0.75rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 5px;
  font-weight: 700;
  margin-left: -5rem;
  box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
  cursor: pointer;
  transition: 0.1s ease-in-out;
}

.home .tagline button:hover {
  outline: #ff7ac6 solid 4px;
  outline-offset: 3px;
  box-shadow: none;
}

.App-link {
  color: #61dafb;
}

.tabs-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.tab-item {
  cursor: pointer;
  padding: 10px 20px;
  margin-right: 10px;
  background-color: #f2f2f2;
  color: #333;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.tab-item:hover {
  background-color: #e0e0e0;
}

.tab-item.active {
  background-color: #007bff;
  color: #fff;
}

.product-content {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 20px;
}

.product-item {
  padding: 20px;
  background-color: #f2f2f2;
  border-radius: 4px;
}

.product-item h3 {
  margin-top: 0;
  margin-bottom: 10px;
}

.product-item p {
  margin: 0;
}

.header {
  width: 100%;
  position: absolute;
  background-color: transparent;
  padding: 0.75rem 4rem;
  display: flex;
  justify-content: space-between;
}

.header h1 {
  color: var(--textbg);
  letter-spacing: 0.25rem;
  font-weight: 800;
  font-size: 2rem;
  text-shadow: 2px 2px 8px var(--bgcolor);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.navbar {
  display: flex;
  align-items: center;
}
.navbar li a {
  display: block;
  color: var(--stextbg);
  margin-left: 10px;
  padding: 0 1.75rem;
  font-size: 1.5rem;
  font-weight: 600;
  text-shadow: 2px 2px 8px var(--bgcolor);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.wrapcard {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding: 1rem 2rem;
  width: 100%;
}
.card {
  width: 30%;
  overflow: hidden;
  background-color: var(--bgcolor);
  border-radius: 5px;
  box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
}

.card img {
  object-fit: cover;
  object-position: top;
}

.card-text {
  color: var(--stextbg);
  padding: 0.25rem 0.5rem;
  margin-bottom: 4px;
}

.card-text p {
  color: var(--desc);
}

.tabs {
  width: 100%;
  background: var(--fbg);
  padding: 20px 30px;
  height: 100vh;
  overflow-x: auto;
}
.tabs .tab-header {
  height: 60px;
  display: flex;
  align-items: center;
}
.tabs .tab-header > div {
  width: calc(100% / 4);
  text-align: center;
  color: var(--stextbg);
  font-weight: 600;
  cursor: pointer;
  font-size: 1rem;
  text-transform: uppercase;
  outline: none;
}
.tabs .tab-header > div > i {
  display: block;
  margin-bottom: 5px;
}
.tabs .tab-header > div.active {
  color: var(--second);
}
.tabs .tab-indicator {
  position: relative;
  width: calc(100% / 4);
  height: 5px;
  background: var(--second);
  left: 0px;
  border-radius: 5px;
  transition: all 500ms ease-in-out;
}
.tabs .tab-body {
  position: relative;
  height: calc(100% - 60px);
  padding: 10px 5px;
  margin-top: 12px;
}
.tabs .tab-body > div {
  position: absolute;
  top: -200%;
  display: none;
  transform: scale(0.9);
  transition: opacity 500ms ease-in-out 0ms, transform 500ms ease-in-out 0ms;
}
.tabs .tab-body > div.active {
  top: 0px;
  display: block;
  transform: scale(1);
}

.about {
  padding-top: 1rem;
  padding-bottom: 2rem;
}

.about h1 {
  color: var(--stextbg);
  font-size: 2.5rem;
}

.about p {
  color: var(--second);
}

.form {
  padding-top: 1rem;
  padding-bottom: 2.35rem;
}
